import http from '../../http-common'

class UnitsSercice {
  getAll () {
    return http.get('Units')
  }

  getAllPagnation (params) {
    return http.get('Units', { params })
  }

  get (id) {
    return http.get(`Units/getById?Id=${id}`)
  }

  search (params) {
    return http.get('Units/Search', { params })
  }

  create (data) {
    return http.post('Units', data)
  }

  update (id, data) {
    return http.put(`Units?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Units?Id=${id}`)
  }
}

export default new UnitsSercice()
