import http from '../../http-common'

class ProductsSercice {
  getAll () {
    return http.get('Products')
  }

  getAllPagnation (params) {
    return http.get('Products', { params })
  }

  get (id) {
    return http.get(`Products/getById?Id=${id}`)
  }

  search (params) {
    return http.get('Products/Search', { params })
  }

  create (data) {
    return http.post('Products', data)
  }

  update (id, data) {
    return http.put(`Products?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Products?Id=${id}`)
  }
}

export default new ProductsSercice()
