<template>
  <div class="contanier">
    <div class="row" style="justify-content: center;">
      <va-card :title="$t('tables.labelOutOrder')" class="flex md8 sm12 xs12">
        <form @submit.prevent="addToSubsfun()">
          <div class="row flex-center">
            <div class="flex md6 sm12 xs12 px-1">
              <label class="input-label"> {{ $t("placeholder.StockName") }} </label>
              <va-select
                :label="$t('placeholder.StockName')"
                v-model="fKStock.value"
                required
                searchable
                textBy="name"
                keyBy="id"
                :options="fKStock.options"
              />
            </div>
            <div class="flex md6 sm12 xs12 px-1">
              <label class="input-label"> {{ $t("placeholder.productName") }} </label>
              <va-select
                :label="$t('placeholder.productName')"
                v-model="fKProduct.value"
                required
                searchable
                textBy="name"
                keyBy="id"
                :options="fKProduct.options"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1">
              <label class="input-label"> {{ $t("placeholder.uintName") }} </label>
              <va-select
                :label="$t('placeholder.uintName')"
                v-model="fKUint.value"
                required
                searchable
                textBy="name"
                keyBy="id"
                :options="fKUint.options"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1">
              <label class="input-label"> {{ $t("placeholder.quantity") }} </label>
              <va-input
                v-model="subs.quantity"
                type="number"
                step="1"
                min="0"
                :placeholder="$t('placeholder.quantity')"
                required
              />
            </div>
            <div class="d-flex justify--center mt-3">
              <va-button type="submit" :disabled="stopBtn" color="success" >
                {{ $t("modal.add") }}
              </va-button>
            </div>
          </div>
          <div class="row">
            <va-data-table
              style="width: 100%;"
              :fields="Sub_fields"
              :data="currentobject.tr_Stock_Subs"
              api-mode
              no-pagination
            >
              <template slot="marker" slot-scope="props">
                {{ props.rowIndex + 1 }}
              </template>

              <template slot="actions" slot-scope="props">
                <va-button
                  flat
                  small
                  color="danger"
                  @click="deletafromsubs(props.rowIndex)"
                  class="ma-0"
                >
                  {{ $t("tables.delete") }}
                </va-button>
              </template>
            </va-data-table>
          </div>

          <div class="d-flex justify--center mt-3">
            <va-button type="button" flat @click="create()" :disabled="stopBtn" class="my-0"
            >{{ $t("modal.ok") }}
            </va-button>
          </div>
        </form>
      </va-card>
    </div>
  </div>
</template>

<script>
import StockService from '../../../services/BasicData/StockService'
import StockTransactionService from '../../../services/stock/StockTransactionService'
import ProductsService from '../../../services/BasicData/ProductsService'
import UnitsService from '../../../services/BasicData/UnitsService'

export default {
  name: 'OutOrder',
  data () {
    return {
      fKProduct: {
        value: '',
        options: [],
      },
      fKStock: {
        value: '',
        options: [],
      },
      fKUint: {
        value: '',
        options: [],
      },
      currentobject: {
        tr_Stock_Subs: [],
      },
      subs: {},
      stopBtn: false,
    }
  },
  watch: {
    'fKProduct.value': function (val) {
      console.log('ff')
      this.fKUint.value = ''
      this.getUints(val.id)
    },
  },
  computed: {

    Sub_fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: 'products',
        title: this.$t('tables.headings.product'),
      }, {
        name: 'stock',
        title: this.$t('tables.headings.stock'),
      },
      {
        name: 'units',
        title: this.$t('tables.headings.units'),
      },
      {
        name: 'quantity',
        title: this.$t('tables.headings.quantity'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  created () {
    this.getproduct()
    // this.getUints()
    this.getStock()
  },
  methods: {
    getproduct () {
      const params = {
        Fk_Company_Activity: 1,
      }
      ProductsService.getAllPagnation(params).then(
        (response) => {
          this.fKProduct.options = response.data
        },
        (err) => {
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    getStock () {
      StockService.getAll().then(
        (response) => {
          this.fKStock.options = response.data
        },
        (err) => {
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    getUints (id) {
      const params = {
        Id_Product: id,
      }
      UnitsService.getAllPagnation(params).then(
        (response) => {
          this.fKUint.options = response.data
        },
        (err) => {
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    create () {
      this.stopBtn = true
      if (this.currentobject.tr_Stock_Subs.length <= 0) {
        this.stopBtn = false
        return this.showToast(this.$t('generic.addProdutTotransfer'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
      this.currentobject.fK_Employees = 1
      this.currentobject.fK_Delivery = 1
      this.currentobject.fK_Tr_Stock_Type = 4
      StockTransactionService.create(this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.currentobject = {
            tr_Stock_Subs: [
            ],
          }
          this.fKProduct.value = ''
          this.fKStock.value = ''
          this.fKUint.value = ''
          this.subs.quantity = null
          this.stopBtn = false

          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    // okCreatefun () {
    //   this.stopBtn = true
    //   if (
    //     this.fKProduct.value !== null &&
    //     this.fKProduct.value !== undefined &&
    //     this.fKProduct.value !== ''
    //   ) {
    //     this.StockId = this.fKProduct.value

    //     this.showStatment = !this.showStatment
    //   } else {
    //     this.stopBtn = false
    //     this.showToast(this.$t('generic.errorChooseStock'), {
    //       icon: 'fa-exclamation',
    //       position: 'bottom-center',
    //       duration: 2500,
    //       type: 'error',
    //     })
    //   }
    // },
    addToSubsfun () {
      this.stopBtn = true
      if (
        this.fKProduct.value !== null &&
        this.fKProduct.value !== '' &&
        this.fKStock.value !== null &&
        this.fKStock.value !== '' &&
        this.fKUint.value !== null &&
        this.fKUint.value !== ''
      ) {
        this.subs.fK_Units = this.fKUint.value.id
        this.subs.fK_Stock = this.fKStock.value.id
        this.subs.fK_Products = this.fKProduct.value.id
        this.subs.units = this.fKUint.value.name
        this.subs.stock = this.fKStock.value.name
        this.subs.products = this.fKProduct.value.name
        var obj = Object.assign({}, this.subs)
        this.currentobject.tr_Stock_Subs.push(obj)
        this.fKProduct.value = ''
        this.fKUint.value = ''
        this.subs.quantity = null
        this.stopBtn = false
      } else {
        this.stopBtn = false
        this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
    deletafromsubs (index) {
      this.currentobject.tr_Stock_Subs.splice(index, 1)
    },
  },
}
</script>

<style lang="scss">
.row-equal .flex {
  .va-card {
    height: 100%;
  }
}

.dashboard {
  .va-card {
    margin-bottom: 0 !important;
  }
}
</style>
