import http from '../../http-common'

class TrStockService {
  getAll () {
    return http.get('Tr_Stock')
  }

  getAllPagnation (params) {
    return http.get('Tr_Stock', { params })
  }

  Seen (params) {
    return http.post('Tr_Stock/Seen', null, { params })
  }

  isFinishToDelivery (params, data) {
    return http.put('Tr_Stock/IsFinishToDelivery', data, { params })
  }

  get (id) {
    return http.get(`Tr_Stock/getById?Id=${id}`)
  }

  create (data) {
    return http.post('Tr_Stock', data)
  }

  search (params) {
    return http.get('Tr_Stock/Search', { params })
  }

  update (id, data) {
    return http.put(`Tr_Stock?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Tr_Stock?Id=${id}`)
  }
}

export default new TrStockService()
