import http from '../../http-common'

class StockSercice {
  getAll () {
    return http.get('Stock')
  }

  getAllPagnation (params) {
    return http.get('Stock', { params })
  }

  get (id) {
    return http.get(`Stock?Id=${id}`)
  }

  search (params) {
    return http.get('Stock/Search', { params })
  }

  create (data) {
    return http.post('Stock', data)
  }

  update (id, data) {
    return http.put(`Stock?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Stock?Id=${id}`)
  }
}

export default new StockSercice()
